<template>
  <router-view/>
</template>

<script>
export default {
  name: 'App',
  mounted(){
    this.onResize();
    window.addEventListener('resize', this.onResize);
  },
  beforeUnmount(){
    window.removeEventListener('resize', this.onResize);
  },
  methods:{
    onResize(){
      this.$store.dispatch('SET_VIEWPORT_ACTION', window.innerWidth);
    }
  }
}
</script>
