import { START_TOAST_ACTION, END_TOAST_ACTION } from './action';
import { START_TOAST_MUTATION, END_TOAST_MUTATION } from './mutation';

export default {
  state: {
    isToastPopup: false,
    toastMsg: null
  },
  actions: {
    [START_TOAST_ACTION](context, msg) {
      this.commit({
        type: START_TOAST_MUTATION,
        msg: msg,
      });
    },
    [END_TOAST_ACTION](context) {
      this.commit({
        type: END_TOAST_MUTATION,
      });
    },
  },
  mutations: {
    [START_TOAST_MUTATION](state, payload) {
      const { msg } = payload;

      state.toastMsg = msg;

      state.isToastPopup = true;
    },
    [END_TOAST_MUTATION](state, payload) {
      state.isToastPopup = false;
    },
  },
}

export { START_TOAST_ACTION, END_TOAST_ACTION };