// import { CHANGE_GNB_ACTION } from './action';
// import { CHANGE_GNB_MUTATION } from './mutation';

export default {
  state: {
    windowWidth: 0,
  },
  actions: {
    SET_VIEWPORT_ACTION(context, windowWidth) {
      this.commit({
        type: 'SET_VIEWPOR_MUTATION',
        windowWidth: windowWidth,
      });
    },
  },
  mutations: {
    SET_VIEWPOR_MUTATION(state, payload) {
      const { windowWidth } = payload;

      state.windowWidth = windowWidth;
    },
  },
}

