import { CHANGE_GNB_ACTION } from './action';
import { CHANGE_GNB_MUTATION } from './mutation';

export default {
  state: {
    isActiveGnb: true,
  },
  actions: {
    [CHANGE_GNB_ACTION](context, isActiveGnb) {
      this.commit({
        type: CHANGE_GNB_MUTATION,
        isActiveGnb: isActiveGnb,
      });
    },
  },
  mutations: {
    [CHANGE_GNB_MUTATION](state, payload) {
      const { isActiveGnb } = payload;

      state.isActiveGnb = isActiveGnb;
    },
  },
}

export { CHANGE_GNB_ACTION };