import { createRouter, createWebHistory } from 'vue-router';
import menuId from '@/constants/menuId';
import routerPath from './routerPath';

const routes = [
  {
    path: routerPath.HOME,
    // name: 'Home',
    // component: () => import('@/views/Home.vue'),
    // component: () => import('@/views/intro/Intro.vue'),
  },
  {
    path: routerPath.INTRO,
    name: 'Intro',
    meta: { 
      menuId: menuId.INTRO,
      pageTitle:'인트로',
      requiresAuth: false
    },
    component: () => import('@/views/intro/view/Intro.vue'),
  },
  {
    path: routerPath.LOGIN,
    name: 'Login',
    meta: { 
      menuId: menuId.INTRO,
      pageTitle:'로그인',
      requiresAuth: false
    },
    component: () => import('@/views/login/view/Login.vue'),
  },
  {
    path: routerPath.SIGNUP,
    name: 'Signup',
    meta: { 
      menuId: menuId.INTRO,
      pageTitle:'회원가입',
      requiresAuth: false
    },
    component: () => import('@/views/signup/view/Signup.vue'),
  },
  {
    path: routerPath.MAP,
    name: 'Map',
    meta: { 
      menuId: menuId.MAP,
      pageTitle:'지도로 검색',
      isFoot: false
    },
    component: () => import('@/views/map/view/Map.vue'),
  },
  {
    path: routerPath.FIELD_LIST,
    name: 'FieldList',
    meta: { 
      menuId: menuId.FIELD,
      subMenuId: menuId.FIELD_LIST,
      pageTitle:'내 현장 보기'
    },
    component: () => import('@/views/field/view/FieldList.vue'),
  },
  {
    path: routerPath.FIELD_WRITE,
    name: 'FieldWrite',
    meta: { 
      menuId: menuId.FIELD,
      subMenuId: menuId.FIELD_WRITE,
      pageTitle:'내 현장 등록'
    },
    component: () => import('@/views/field/view/FieldWrite.vue'),
  },
  {
    path: routerPath.NEW_FIELD_SURVEY_ASK_WRITE,
    name: 'NewFieldSurveyAskWrite',
    meta: { 
      menuId: menuId.FIELD,
      subMenuId: menuId.FIELD_WRITE,
      pageTitle:'신축 답사 요청'
    },
    component: () => import('@/views/field/view/NewFieldSurveyAskWrite.vue'),
  },
  {
    path: routerPath.NEW_FIELD_WRITE,
    name: 'NewFieldWrite',
    meta: { 
      menuId: menuId.FIELD,
      subMenuId: menuId.FIELD_WRITE,
      pageTitle:'신축 현장 등록'
    },
    component: () => import('@/views/field/view/NewFieldWrite.vue'),
  },
  {
    path: routerPath.NOTIFY,
    name: 'Notify',
    meta: { 
      menuId: menuId.NOTIFY,
      subMenuId: menuId.NOTIFY,
      pageTitle:'알림관리'
    },
    component: () => import('@/views/notify/view/Notify.vue'),
  },
  {
    path: routerPath.PAYMENT_USETICKT,
    name: 'PaymentUseTicket',
    meta: { 
      menuId: menuId.PAYMENT,
      subMenuId: menuId.PAYMENT_USETICKT,
      pageTitle:'상품구매'
    },
    component: () => import('@/views/payment/view/UseTicketPayment.vue'),
  },
  {
    path: routerPath.PAYMENT_NOTIFY,
    name: 'PaymentNotify',
    meta: { 
      menuId: menuId.PAYMENT,
      subMenuId: menuId.PAYMENT_NOTIFY,
      pageTitle:'알림권'
    },
    component: () => import('@/views/payment/view/NotifyPayment.vue'),
  },
  {
    path: routerPath.PAYMENT_GOLD,
    name: 'PaymentGold',
    meta: { 
      menuId: menuId.PAYMENT,
      subMenuId: menuId.PAYMENT_GOLD,
      pageTitle:'골드상품'
    },
    component: () => import('@/views/payment/view/GoldPayment.vue'),
  },
  {
    path: routerPath.MYPAGE,
    name: 'Mypage',
    meta: { 
      menuId: menuId.MYPAGE,
      subMenuId: menuId.MYPAGE,
      pageTitle:'마이페이지'
    },
    component: () => import('@/views/mypage/view/Mypage.vue'),
  },
  // { path: '*', redirect: routerPath.INTRO },
]
const router = createRouter({
  history: createWebHistory(),
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (from.path === to.path) {
      return;
    }
    if (savedPosition) {
      return savedPosition;
    } else {
      return { x: 0, y: 0 };
    }
  },
});

router.beforeEach(async (to, _from, next) => {
  // admin/user에 따른 홈.
  if (to.path === routerPath.HOME) {
    next({
      name: 'Intro',
    });
    return;
  }else{
    next()
  }
})

export default router
